.bPSS {
  .components-base-control__help {
    margin: 0
  }
  .button-danger {
    background: #be0909;
    color: #fff;
  }
  .button-small {
    height: 30px;
    font-weight: 600;
  }
  .button-primary, .is-primary {
    color: #fff !important;
  }
}
.BColorButton,
.BColorButtonContainer {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: 0px;
  border: none;
  cursor: pointer;
}
.BColorButton:hover {
  cursor: pointer;
}
.BColorButtonContainer {
  width: 32px;
  height: 32px;
  background-image: linear-gradient(45deg, #ddd 25%, transparent 0), linear-gradient(-45deg, #ddd 25%, transparent 0), linear-gradient(45deg, transparent 75%, #ddd 0), linear-gradient(-45deg, transparent 75%, #ddd 0);
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0;
  border: 1px solid #e9e9e9;
  box-sizing: border-box;
}
.bColorReset .dashicons-image-rotate {
  text-align: center;
}
.bPSS .components-panel__row input[type="text"] {
  // border-radius: 5px;
  padding-right: 10px;
  outline: 0;
  border: 1px solid #c7c3c3;
}
.components-panel__row.align-right button {
  margin-left: auto;
}

.bButtonGroup button {
  height: 25px;
  padding: 0px 5px;
  border: 1px solid #ddd;
  background: #ffffff;
  border-radius: 2px;
  cursor: pointer;
}
.bButtonGroup button:focus {
  outline: 0px !important;
  box-shadow: 0 0 0 0px #007cba !important;
}
button[bisactive="true"] {
  background: #061b9c;
  color: #ffffff !important;
  fill: #ffffff !important;
  border-radius: 3px;
}
button[bisactive="true"] svg {
  fill: #ffffff;
}
.bPSS [role="tablist"] {
  display: flex;
  justify-content: space-between;
}
.bPSS [role="tablist"] button {
  flex: 1;
  justify-content: center;
}
.bPSS [role="tablist"] button[aria-selected="true"] {
  background: #007cba;
  color: #fff;
}
.bPSS .label {
  font-weight: bold;
}
.components-panel__row.width-100 div {
  width: 100%;
}
.components-panel__row.width-100 input {
  width: 220px;
  max-width: 100%;
}
.h5vpUrlInput h3 {
  margin: 0 10px;
  text-transform: uppercase;
  display: inline;
}
.h5vpUrlInput input {
  width: 300px;
  padding: 8.5px 15px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.h5vpUrlInput {
  margin: -4px;
}
.h5vpUrlInput button {
  margin-left: 20px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.bMediaUpload button {
  position: absolute;
  right: 6px;
  height: 15px;
  padding: 3px !important;
  color: #fff !important;
  outline: 0;
  border-radius: 3px;
}
.bMediaUpload button:focus {
  outline: 0;
  box-shadow: 0 0 0 0px #007cba !important;
}
.subtitleItem button {
  margin-top: -10px;
  padding: 0;
}

.h5vp_pro_label {
  margin: 0 4px;
  line-height: 1;
  background: var(--wp-admin-theme-color);
  color: #fff;
  padding: 3px 6px;
  border-radius: 9999px;
  font-size: 10px;
  display: inline-block;
}
.generatedThumbs canvas {
  margin-right: 10px;
  cursor: pointer;
}
.bPSS .components-button-group button {
  box-shadow: 0 0 0;
  &:focus {
    outline: none;
    box-shadow: none;
  }
  &.button-primary {
    color: #fff;
  }
}
.generateThumbButton {
  text-align: center;
  margin-bottom: 20px;
}
.stickyPosition .components-base-control {
  margin-bottom: 0;
}
.stickyPosition.components-panel__row .components-base-control {
  margin-bottom: 0;
}
.stickyPosition.components-panel__row .components-base-control__field {
  margin-bottom: 0;
}
.popupBtn {
  text-align: center;
  margin: 20px 0;
}
.popupBtn button {
  padding: 0.4em 1.5em;
  outline: none;
  border: 1px solid lightgoldenrodyellow;
  border-radius: 5px;
  font-size: 17px;
  cursor: pointer;
  background: #006ba1;
  color: #fff;
}

.h5vp_player {
  pointer-events: none;
}

.bPSS .block-editor-block-inspector .components-base-control {
  margin-bottom: 8px;
}
.bMediaUpload .components-base-control input {
  padding-right: 0px;
}
.block-editor-block-inspector .bPSS .components-base-control {
  margin-bottom: 0;
}
.block-editor-block-inspector .bPSS .components-base-control.components-toggle-control {
  margin-bottom: 15px;
}
.bButtonGroup {
  margin: 0px;
}